import React from 'react';
import {  Row,  Col } from 'antd';
import { Alert , Card} from 'antd';
import './Services.css'



class Services extends React.Component {
	

render (){

	return(
			<div>	
                <div className="head">
                    <Row>
                        <Col>
                          <p> Services</p>
                        </Col>
                    
                    </Row>   
                </div>
                <div>
                    <Row>
                        <Col md={12} lg={12}>
                            <Card type="inner" title="Recruitment"  style={{ margin:"5%", width: "80%" }}>
                                <p className="note">
                                Cambridge Care Services  is a specialist provider of nursing staff across a number
                                 of clients in the healthcare sector (NHS trust, Private hospital, Nursing homes). 
                                 As a small team, we are able to offer a personal touch to our service - ensuring 
                                 that you only ever deal with one or two key consultants. We pride ourselves on our
                                  ability to find temporary and permanent nursing positions, whether you are a nurse, 
                                  health care assistant who is looking for better prospects, or a healthcare unit that
                                   requires a short notice staffing contingent. We recruit staff domestically and
                                    abroad.
                                </p>
                               
                            </Card>
                         
                        </Col>
                        <Col md={12} lg={12}>

                            <Card type="inner" title="Quality"  style={{ margin:"5%", width: "80%" }}>
                                 <Alert message="All of our candidates are interviewed by a senior nurse and passed through a rigorous assessment prior to working." type="success" />
                          
                         
                        
                             </Card>
                           
                             <Card type="inner" title="Compliancy"  style={{ margin:"5%", width: "80%" }}>
                             
                                 <Alert message="All of our candidates are interviewed by a senior nurse and passed through a rigorous assessment prior to working." type="warning" />
                            
                         
                              </Card>
                           

                            
                        
                        </Col>
           
                    </Row>
                    <Row>
                    <Col md={12} lg={12}>

                        <Card type="inner" title="Locum Doctors"  style={{ margin:"5%", width: "80%" }}>
                        <p className="note"> We offer locum and temporary contract opportunities for Locum doctors and 
                        GPs throughout the UK. Our dedicated team have strong industry experience and can provide a 
                        range of individually tailored workforce solutions, to providing a fully managed relief panel 
                        to cover last minute and planned absences.
                            </p>
                    
                         </Card>
                       
                    </Col>
                    <Col md={12} lg={12}>

                        <Card type="inner" title="Our Full Service"  style={{ margin:"5%", width: "80%" }}>
                           <Alert message="Locum and contract staffing solutions. A 24/7 on-call service is available." type="success" />
                           <Alert message="Ad hoc cover for annual leave, sick leave or increased staffing demands." type="success" />
                           <Alert message="In-house regular training courses." type="success" />
                           <Alert message="A comprehensive pre-employment candidate vetting service." type="success" />
                        </Card>

                     
                        
                    </Col>
       
                </Row>
                
                </div>

			</div>
		)
	}
}
export default Services;







